import logo from "./logo.svg";
import "./App.css";
import { useCallback, useEffect, useState } from "react";
import axios from "axios";
import { Box, Tab, Tabs } from "@mui/material";
import Typography from "@mui/material/Typography";
import * as React from "react";
import Modal from "@mui/material/Modal";
import ReactHlsPlayer from "react-hls-player";
import Comp from "./comp";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: "50%",
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 0,
  p: 0,
};

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 0 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

function App() {
  const [data, setData] = useState({});

  const getData = useCallback(async () => {
    try {
      const { data } = await axios.get(
        "http://3.109.32.27:9999/api/stream/upcoming/data"
      );
      if (data) {
        setData(data.data);
      }
    } catch (e) {
      console.log(e);
    }
  }, []);

  useEffect(() => {
    getData();
  }, [getData]);

  console.log(data);

  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const [open, setOpen] = React.useState(false);
  const [url, setUrl] = useState("");

  const handleOpen = (item) => {
    if (item.meta.subCategory !== "Gaming Streams") {
      window.open(item.url);
      return;
    }
    setUrl(
      `https://live-cdn.glance.com/${item?.hostDetails?.channel?.toLowerCase()}/manifest.m3u8`
    );
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
    setUrl("");
  };

  return (
    <div className="App">
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <ReactHlsPlayer
            src={url}
            autoPlay={true}
            controls={true}
            width="100%"
            height="auto"
          />
        </Box>
      </Modal>
      <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
        <Tabs
          value={value}
          onChange={handleChange}
          aria-label="basic tabs example"
        >
          <Tab label="Today" {...a11yProps(0)} />
          <Tab label="Tomorrow" {...a11yProps(1)} />
          <Tab label="Upcoming" {...a11yProps(2)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        {data?.today?.map((item) => {
          return <Comp handleOpen={handleOpen} item={item} />;
        })}
      </TabPanel>
      <TabPanel value={value} index={1}>
        {data?.tomorrow?.map((item) => {
          return <Comp handleOpen={handleOpen} item={item} />;
        })}
      </TabPanel>
      <TabPanel value={value} index={2}>
        {data?.upcoming?.map((item) => {
          return <Comp handleOpen={handleOpen} item={item} />;
        })}
      </TabPanel>
    </div>
  );
}

export default App;
